* {
	box-sizing: border-box;
}
body {
	margin: 0;
	padding: 0 0 50px;
	background-color: #fafbfc;
	color: #24292e;
}
.header {
	text-align: center;
	padding: 20px;
	background-color: #24292e;
	color: #fff;
	margin-bottom: 50px;
	font-size: 20px;
}
.wrapper {
	max-width: 1200px;
	margin: 0 auto;
}
.btn {
	text-align: center;
	padding: 8px 14px;
	background-color: #daa520;
	border: 1px solid;
	color: #fff;
	cursor: pointer;
	transition: background-color 0.3s, transform 0.3s;
	outline: none;
	margin-left: auto;
	display: block;
	max-width: 150px;
}
.btn:hover {
	background-color: #ac821a;
}
.btn:active {
	transform: scale(0.95);
}
.csv-input {
	display: none;
}

.table {
	width: 100%;
	margin: 30px 0;
	border-collapse: collapse;
}

.table th,
.table td {
	padding: 8px;
	border: 1px solid #000;
}
.table th {
	background-color: #f1f8ff;
	color: #24292e;
	padding: 14px 10px;
}
.table .red {
	background-color: rgba(236, 25, 25, 0.527);
}

.msg {
	max-width: 400px;
	text-align: center;
	padding: 15px 10px;
	border: 1px solid #000;
	margin: 30px auto 10px;
	font-size: 20px;
	font-weight: 700;
}
.msg-error {
	border-color: red;
}
.msgToUpload {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	font-weight: 700;
	font-size: 22px;
	padding: 10px 50px 0 0;
}
.msgToUpload img {
	width: 60px;
	margin-left: 20px;
}
.msgToUpload p {
	border: 1px solid #000;
	padding: 20px 15px;
}
.spinner {
	max-width: 300px;
	margin: 0 auto;
	text-align: center;
}
.spinner img {
	max-width: 70px;
}

@media (max-width: 1110px) {
	body {
		font-size: 12px;
	}
	.app {
		overflow: hidden;
	}
	.wrapper {
		overflow: scroll;
	}
	.table {
		width: auto;
	}
	.table th,
	.table td {
		padding: 4px;
	}
}
